  height:3px;
  position:absolute;
  background:gold;
  transition:all .1s ease-in-out;
  transform-origin:50% 50%
}

nav .burger span:nth-child(1){
  top:1rem
}

nav .burger span:nth-child(2){
  bottom:1rem;
  top:1rem;
  margin:auto
}

nav .burger span:nth-child(3){
  bottom:1rem
}

nav .burger.cancel span{
  transition:all .4s ease-in-out;
  transform-origin:50% 50%;
  top:0;
  bottom:0;
  margin:auto
}

nav .burger.cancel span:nth-child(1){
  transform:rotate(45deg)
}

nav .burger.cancel span:nth-child(2){
  display:none
}

nav .burger.cancel span:nth-child(3){
  transform:rotate(-45deg)
}

